.app__profiles {
  display: flex;
  justify-content: space-evenly;
  padding: 4rem 10rem;

  @media screen and (max-width: 600px) {
    flex-direction: column;
    padding: 0;
  }
}

.app__profile-item {
  display: flex;
  flex-direction: column;

  width: 23%;

  img {
    width: 100%;
    border-radius: 15px;
    object-fit: cover;

    @media screen and (max-width: 600px) {
      border-radius: 10px;
    }
  }

  @media screen and (max-width: 600px) {
    width: 100%;
    padding: 1rem 5rem;
  }
}
